.reply-form{
  margin: .5% 0 0em 15%;
  width: 50%;
  padding: 1em;
}
.reply-form textarea{
  height: 6em;
  width:100%;
}
.reply-button{
  margin: .5% 0 0em 19%;
  background:  #0690cf;
  color: white;
  border: none;
  border-radius: .2em;
  width: 60%;
  height: 2em;
  box-shadow:3px 3px  #046694;
}


.reply-button:active{ 
  box-shadow: inset 0 0 0 25px  #046694;
}

@media (max-width:600px){
.reply-form{
    width: 80%  
  }
}