.grid-container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr;
  grid-template-areas: "Form Comments";
}

.Form { grid-area: Form; }

.Comments { grid-area: Comments; 
            }

@media (max-width:1560px){
  .grid-container {
    display: flex;
    flex-direction:column;
    align-items: center;
  }
  .Comments{
    width: 70%;
  }
  .Form{
  width: 100%;
  }
}
@media (max-width:600px){
  .Comments{
    width: 80%;
  }
  }