.post-editor{
  margin: 7% 0 -10% 40%;
  width: 85%;
  padding: 1em;
}
.post-editor textarea{
  height: 7em;
  width: 100%;
}
.post-editor-button{
  margin-left: 28%;
  background: rgb(221, 14, 14);
  color: white;
  border: none;
  border-radius: .2em;
  width: 50%;
  height: 2em;
  margin-top: 8%;
  box-shadow:3px 3px rgb(136, 7, 7);
}
.post-editor-input{
  margin-bottom: -2em;
}

.post-editor-button:active{ 
  box-shadow: inset 0 0 0 25px rgb(136, 7, 7);
}


@media (max-width:1560px){
  .post-editor{
    margin: 0 auto;
    width: 50%  
  }
}

@media (max-width:900px){
  .post-editor{
    width: 80%  
  }
}
@media (max-width:600px){
  .post-editor{
    width: 100%  
  }
}