
.post{
  margin: 4.3% 0% -2% 20%;
}
.post-container{
  margin-bottom: 2%;
  width: 70%;
}

.post-name{
  font-weight: bold;
  color: #0690cf;
  font-size: 1.2
}

.post-body{
  padding: 1em;
  width: 100%;
  border: solid 1px rgb(211, 211, 211);
  border-radius: .2em;
  background: rgb(255, 255, 255);
}

/* Icons */
.post-icons #upvote, #downvote, #reply, i{
  cursor: pointer;
}
.post-icons #upvote, #downvote{
  margin-right: .3em;
  margin-left: .3em;
}
.post-icons #reply{
  margin-left: .5em;
}
.post-icons i{
  font-family: Arial, Helvetica, sans-serif;
  font-style: italic;
  font-weight: bold;
  margin-left: .2em;
  color: #0690cf;
}


/* Animations */
.post{
  /* for chrome and safari*/
  -webkit-animation-duration: .5s;
  -webkit-animation-name: slideinPost;

  /*for firefox*/
  -moz-animation-duration: .5s;
  -moz-animation-name: slideinPost;

  /* for opera*/
  -o-animation-duration: .5s;
  -o-animation-name: slideinPost;

  /* Standard syntax*/
  animation-duration: .5s;
  animation-name: slideinPost;
}

@-webkit-keyframes slideinPost {
  from {
    margin-left: 5%;
  }
  to {
    margin-left: 20%;
  }
}

@keyframes slideinPost {
  from {
    margin-left: 5%;
  }
  to {
    margin-left: 20%;
  }
}



@media (max-width:1560px){
  .post{
    margin-left: 15%;
    width: 100%;
  }
}

 @media (max-width:600px){
   .post{
    margin-left: 0;
   }
  .post-container{
    width: 100%;
  }
  @keyframes slideinPost {
  from {
    margin-left: -5%;
  }
  to {
    margin-left: 0;
  }
}
@-webkit-keyframes slideinPost {
  from {
    margin-left: 5%;
  }
  to {
    margin-left: 0;
  }
}

}


.reply-form{
  margin: .5% 0 0em 15%;
  width: 50%;
  padding: 1em;
}
.reply-form textarea{
  height: 6em;
  width:100%;
}
.reply-button{
  margin: .5% 0 0em 19%;
  background:  #0690cf;
  color: white;
  border: none;
  border-radius: .2em;
  width: 60%;
  height: 2em;
  box-shadow:3px 3px  #046694;
}


.reply-button:active{ 
  box-shadow: inset 0 0 0 25px  #046694;
}

@media (max-width:600px){
.reply-form{
    width: 80%  
  }
}

.reply{
  margin: 0em 0em 0em 1%;
}
.reply-border{
  border-left: 1px solid lightgray;
  margin-left: 5%;
  max-width:100%;
}
.reply-name{
  font-weight: bold;
  color: #0690cf;
  font-size: 1.1em;
} 
.reply-body{
  padding: 1em;
  width: 77%;
  border: solid 1px rgb(211, 211, 211);
  border-radius: .2em;
  background: rgb(255, 255, 255);

  /* border-color: red ; */
}
.reply-icons #r-upvote, #r-downvote, #r-reply, i{
  cursor: pointer;
}
.reply-icons #r-upvote, #r-downvote{
  margin-right: .3em;
  margin-left: .3em;
}
.reply-icons #r-reply{
  margin-left: .5em;
}
.reply-icons #r-upvote, #r-downvote, #r-reply{
  cursor: pointer;
}

.reply-icons i{
  font-family: Arial, Helvetica, sans-serif;
  font-style: italic;
  font-weight: bold;
  margin-left: .2em;
  color: #0690cf;
}

/* Animations */
.reply{
  /* for chrome and safari*/
  -webkit-animation-duration: .5s;
  -webkit-animation-name: slideinReply;

  /*for firefox*/
  -moz-animation-duration: .5s;
  -moz-animation-name: slideinReply;

  /* for opera*/
  -o-animation-duration: .5s;
  -o-animation-name: slideinReply;

  /* Standard syntax*/
  animation-duration: .5s;
  animation-name: slideinReply;
}

@-webkit-keyframes slideinReply {
  from {
    margin-left: 20%;
  }
  to {
    margin-left: 1%;
  }
}

@keyframes slideinReply {
  from {
    margin-left: 20%;
  }
  to {
    margin-left: 1%;
  }
}

/* Media Querys */
@media (max-width:900px){
   .reply-body{
    margin: 1em;
  }
}
.post-editor{
  margin: 7% 0 -10% 40%;
  width: 85%;
  padding: 1em;
}
.post-editor textarea{
  height: 7em;
  width: 100%;
}
.post-editor-button{
  margin-left: 28%;
  background: rgb(221, 14, 14);
  color: white;
  border: none;
  border-radius: .2em;
  width: 50%;
  height: 2em;
  margin-top: 8%;
  box-shadow:3px 3px rgb(136, 7, 7);
}
.post-editor-input{
  margin-bottom: -2em;
}

.post-editor-button:active{ 
  box-shadow: inset 0 0 0 25px rgb(136, 7, 7);
}


@media (max-width:1560px){
  .post-editor{
    margin: 0 auto;
    width: 50%  
  }
}

@media (max-width:900px){
  .post-editor{
    width: 80%  
  }
}
@media (max-width:600px){
  .post-editor{
    width: 100%  
  }
}
.grid-container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr;
  grid-template-areas: "Form Comments";
}

.Form { grid-area: Form; }

.Comments { grid-area: Comments; 
            }

@media (max-width:1560px){
  .grid-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction:column;
            flex-direction:column;
    -webkit-align-items: center;
            align-items: center;
  }
  .Comments{
    width: 70%;
  }
  .Form{
  width: 100%;
  }
}
@media (max-width:600px){
  .Comments{
    width: 80%;
  }
  }
