
.reply{
  margin: 0em 0em 0em 1%;
}
.reply-border{
  border-left: 1px solid lightgray;
  margin-left: 5%;
  max-width:100%;
}
.reply-name{
  font-weight: bold;
  color: #0690cf;
  font-size: 1.1em;
} 
.reply-body{
  padding: 1em;
  width: 77%;
  border: solid 1px rgb(211, 211, 211);
  border-radius: .2em;
  background: rgb(255, 255, 255);

  /* border-color: red ; */
}
.reply-icons #r-upvote, #r-downvote, #r-reply, i{
  cursor: pointer;
}
.reply-icons #r-upvote, #r-downvote{
  margin-right: .3em;
  margin-left: .3em;
}
.reply-icons #r-reply{
  margin-left: .5em;
}
.reply-icons #r-upvote, #r-downvote, #r-reply{
  cursor: pointer;
}

.reply-icons i{
  font-family: Arial, Helvetica, sans-serif;
  font-style: italic;
  font-weight: bold;
  margin-left: .2em;
  color: #0690cf;
}

/* Animations */
.reply{
  /* for chrome and safari*/
  -webkit-animation-duration: .5s;
  -webkit-animation-name: slideinReply;

  /*for firefox*/
  -moz-animation-duration: .5s;
  -moz-animation-name: slideinReply;

  /* for opera*/
  -o-animation-duration: .5s;
  -o-animation-name: slideinReply;

  /* Standard syntax*/
  animation-duration: .5s;
  animation-name: slideinReply;
}

@-webkit-keyframes slideinReply {
  from {
    margin-left: 20%;
  }
  to {
    margin-left: 1%;
  }
}

@-moz-keyframes slideinReply {
  from {
    margin-left: 20%;
  }
  to {
    margin-left: 1%;
  }
}

@-o-keyframes slideinReply {
  from {
    margin-left: 20%;
  }
  to {
    margin-left: 1%;
  }
}

@keyframes slideinReply {
  from {
    margin-left: 20%;
  }
  to {
    margin-left: 1%;
  }
}

/* Media Querys */
@media (max-width:900px){
   .reply-body{
    margin: 1em;
  }
}