.post{
  margin: 4.3% 0% -2% 20%;
}
.post-container{
  margin-bottom: 2%;
  width: 70%;
}

.post-name{
  font-weight: bold;
  color: #0690cf;
  font-size: 1.2
}

.post-body{
  padding: 1em;
  width: 100%;
  border: solid 1px rgb(211, 211, 211);
  border-radius: .2em;
  background: rgb(255, 255, 255);
}

/* Icons */
.post-icons #upvote, #downvote, #reply, i{
  cursor: pointer;
}
.post-icons #upvote, #downvote{
  margin-right: .3em;
  margin-left: .3em;
}
.post-icons #reply{
  margin-left: .5em;
}
.post-icons i{
  font-family: Arial, Helvetica, sans-serif;
  font-style: italic;
  font-weight: bold;
  margin-left: .2em;
  color: #0690cf;
}


/* Animations */
.post{
  /* for chrome and safari*/
  -webkit-animation-duration: .5s;
  -webkit-animation-name: slideinPost;

  /*for firefox*/
  -moz-animation-duration: .5s;
  -moz-animation-name: slideinPost;

  /* for opera*/
  -o-animation-duration: .5s;
  -o-animation-name: slideinPost;

  /* Standard syntax*/
  animation-duration: .5s;
  animation-name: slideinPost;
}

@-webkit-keyframes slideinPost {
  from {
    margin-left: 5%;
  }
  to {
    margin-left: 20%;
  }
}

@-moz-keyframes slideinPost {
  from {
    margin-left: 5%;
  }
  to {
    margin-left: 20%;
  }
}

@-o-keyframes slideinPost {
  from {
    margin-left: 5%;
  }
  to {
    margin-left: 20%;
  }
}

@keyframes slideinPost {
  from {
    margin-left: 5%;
  }
  to {
    margin-left: 20%;
  }
}



@media (max-width:1560px){
  .post{
    margin-left: 15%;
    width: 100%;
  }
}

 @media (max-width:600px){
   .post{
    margin-left: 0;
   }
  .post-container{
    width: 100%;
  }
  @keyframes slideinPost {
  from {
    margin-left: -5%;
  }
  to {
    margin-left: 0;
  }
}
@-webkit-keyframes slideinPost {
  from {
    margin-left: 5%;
  }
  to {
    margin-left: 0;
  }
}

}

